@import url('https://fonts.googleapis.com/css2?family=Imperial+Script&display=swap');

* {
    margin: 0;
    padding: 0;
}

audio {
    position: fixed;
    top: 5%;
    left: 5%;
    z-index: 10;
}


body {

    background: rgb(0, 0, 0);
    background: linear-gradient(180deg, rgba(0, 0, 0, 1) 16%, rgba(3, 0, 57, 1) 71%, rgba(0, 0, 38, 1) 100%);
    position: relative;
    z-index: 1;

    height: 1000px;
    font-family: 'Imperial Script', cursive;
    overflow-x: hidden;
}

section {
    overflow: hidden;
}

.Luna {
    width: 150px;
    height: 150px;
    border-radius: 50%;
    background-color: #e3e3d0;
    position: absolute;
    top: 100px;
    left: 70%;
    box-shadow: 0 0 20px white;



}

.Tierra {
    position: relative;
    top: 8px
}

.TierraUno div {
    position: absolute;
    height: 150px;
    overflow: hidden;
    top: -15vh;
    right: 0;
    left: 0;
    z-index: 5;

}

.TierraUno div svg {
    height: 100%;
    width: 100%;
}

.TierraUno div svg path {
    stroke: none;
    fill: #FFFAFA;
}


.TierraDos div {
    position: absolute;
    height: 150px;
    overflow: hidden;
    top: -20vh;
    right: 0;
    left: 0;
    z-index: 3;

}

.TierraTres div svg {
    height: 100%;
    width: 100%;
}

.TierraTres div svg path {
    stroke: none;
    fill: #808080;
}

.TierraTres div {
    position: absolute;
    height: 150px;
    overflow: hidden;
    top: -20vh;
    right: 0;
    left: 0;
    z-index: 2;

}

.TierraDos div svg {
    height: 100%;
    width: 100%;
}

.TierraDos div svg path {
    stroke: none;
    fill: #b9b9b9;
}

.Muneco {
    position: absolute;
    top: -35vh;
    left: 20vw;
    z-index: 4;
}

.MunecoBody {
    position: relative;
}

.Cabeza {
    position: absolute;
    width: 90px;
    height: 90px;
    background-color: white;
    border-radius: 50%;
    left: 35px;
}

.ojoD {
    position: absolute;
    width: 10px;
    height: 10px;
    background-color: black;
    border-radius: 50%;
    top: 20px;
    left: 30px;
}

.ojoI {
    position: absolute;
    width: 10px;
    height: 10px;
    background-color: black;
    border-radius: 50%;
    top: 15px;
    left: 60px;
}

.Nariz {
    position: absolute;
    width: 30px;
    height: 30px;
    background-color: rgb(248, 58, 0);
    clip-path: polygon(100% 26%, 6% 36%, 18% 77%);
    top: 15px;
    left: 50px;
}

.Boca {
    position: absolute;
    width: 30px;
    height: 30px;
    background-color: black;

    top: 25px;
    left: 40px;
    clip-path: polygon(32% 96%, 67% 95%, 77% 90%, 87% 83%, 70% 100%, 30% 100%, 4% 73%, 24% 90%);
}

.Pecho {
    position: absolute;
    width: 135px;
    height: 135px;
    background-color: white;
    border-radius: 50%;
    top: 55px;
    left: 20px;

}

.Piez {
    position: absolute;
    width: 200px;
    height: 200px;
    background-color: white;
    border-radius: 50%;
    top: 150px;
}

.Nieve {
    background-color: #FFFAFA;
    width: 100%;
    height: 100%;
    position: absolute;
    z-index: 10;
}

.MerryChristmas {
    padding-top: 10vw;
    color: black;
    font-size: 10vw;
    text-align: center;
    top: 100px;
    animation: animacion 4s infinite;
}

@keyframes animacion {
    0% {
        font-size: 10vw;
        top: 100px;
    }

    50% {
        font-size: 15vw;
        top: 10px;
    }

    100% {
        font-size: 10vw;
        top: 100px;
    }
}

@media screen and (max-width: 768px) {
    audio {
        width: 100px;
        height: 20px;
        top: 0;
        left: 0;
    }
}


@media screen and (max-width: 652px) {


    .Muneco {

        top: -35vh;
        left: 20vw;

    }

    .Cabeza {
        position: absolute;
        width: 25px;
        height: 25px;
        background-color: white;
        border-radius: 50%;
        left: 8px;
        top: 195px;
    }

    .ojoD {
        position: absolute;
        width: 3px;
        height: 3px;
        background-color: black;
        border-radius: 50%;
        top: 10px;
        left: 10px;
    }

    .ojoI {
        position: absolute;
        width: 3px;
        height: 3px;
        background-color: black;
        border-radius: 50%;
        top: 7px;
        left: 20px;
    }

    .Nariz {
        position: absolute;
        width: 10px;
        height: 10px;
        background-color: rgb(248, 58, 0);
        clip-path: polygon(100% 26%, 6% 36%, 18% 77%);
        top: 8px;
        left: 15px;
    }

    .Boca {
        position: absolute;
        width: 30px;
        height: 30px;
        background-color: transparent;

        top: 25px;
        left: 40px;
        clip-path: polygon(32% 96%, 67% 95%, 77% 90%, 87% 83%, 70% 100%, 30% 100%, 4% 73%, 24% 90%);
    }

    .Pecho {
        position: absolute;
        width: 35px;
        height: 35px;
        background-color: white;
        border-radius: 50%;
        top: 210px;
        left: 5px;

    }

    .Piez {
        position: absolute;
        width: 50px;
        height: 50px;
        background-color: white;
        border-radius: 50%;
        top: 230px;
    }

    .Luna {
        width: 80px;
        height: 80px;
        border-radius: 50%;
        background-color: #e3e3d0;
        position: absolute;
        top: 10px;
        left: 70%;
        box-shadow: 0 0 20px white;



    }
}